.drawer .header {
  padding: 16px;
  text-align: left;
  background: #E1F5FE; }
  .drawer .header .logoff {
    float: right;
    background: #FFCDD2;
    padding: 10px 12px 8px;
    border-radius: 50%;
    cursor: pointer; }
    .drawer .header .logoff:hover {
      background: #EF9A9A; }
  .drawer .header .profile-pic {
    border-radius: 50%;
    height: 50px;
    width: 50px; }
    .drawer .header .profile-pic.no-pic {
      background: #A5D6A7;
      padding: 15px 0;
      margin-bottom: 8px;
      text-align: center;
      font-weight: bold;
      color: #777; }
  .drawer .header .name, .drawer .header .email {
    font-size: 0.8rem;
    color: #777; }
  .drawer .header .name {
    font-weight: 600; }

.drawer .active {
  background: rgba(0, 0, 0, 0.2); }



